import React, { useState } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import {
  MDBAnimation,
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardHeader,
  MDBCardBody,
  MDBCardText,
  MDBCardFooter,
  MDBBtn,
  MDBIcon
} from "mdbreact";
import MiniCart from "./MiniCart";

export default function Carrinho() {
  const [bodyClass, setBodyClass] = useState("hideCardBody");
  const cart = useSelector(state => state.cart);

  function toggleCardBody() {
    if (bodyClass === "hideCardBody") {
      setBodyClass("showCardBody");
    } else {
      setBodyClass("hideCardBody");
    }
    if (cart.items.length === 0) {
      setBodyClass("hideCardBody");
    }
  }

  function getFooter() {
    if (cart.items.length !== 0) {
      return (
        <MDBCardFooter>
          <MDBBtn color="success" className="w-100">
            Finalizar Compra
          </MDBBtn>
        </MDBCardFooter>
      );
    }
  }
  return (
    <div>
      <MDBAnimation type="slideInBottom">
        <MDBContainer>
          <MDBRow>
            <MDBCol size="12">
              <MDBCard style={{ overflow: "auto" }}>
                <MDBCardHeader color="success-color" onClick={toggleCardBody}>
                  <div className="d-flex flex-row">
                    <div className="flex-grow-1 ">
                      <strong>Seu carrinho</strong>
                    </div>
                    <div className="border-left border-white text-right px-2">
                      <MiniCart />
                    </div>
                    <div className="text-right">R$ {cart.total}</div>
                  </div>
                </MDBCardHeader>
                <MDBCardBody
                  className={bodyClass}
                  style={{ maxHeight: "30vh" }}
                >
                  <MDBCardText>
                    {cart.items.length === 0 ? <h5>Vazio</h5> : ""}
                    {cart.items.map(item => (
                      <MDBRow key={item.id}>
                        <MDBCol>{item.name}</MDBCol>
                        <MDBCol>{item.price}</MDBCol>
                        <MDBCol>
                          <MDBIcon icon="minus-circle" />1
                          <MDBIcon icon="plus-circle" />
                        </MDBCol>
                      </MDBRow>
                    ))}
                  </MDBCardText>
                  {getFooter()}
                </MDBCardBody>
              </MDBCard>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      </MDBAnimation>
    </div>
  );
}
